

.state1 {
    background: lightblue url("./firstRes.jpg") no-repeat center;
    background-size: cover;
    height: 85vh;
    position: relative;
    width: 100%;
    z-index: 1;
}

.animationFirst {
    animation-name: example3;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
}

@keyframes example3 {
    0% {opacity: 0; left: -100%}
    30% {opacity: 1}
    100% {opacity: 1; left: 0}
}

.transitionFirstAndSecond1 {
    background: aqua url("./firstAndSecondRes.jpg") no-repeat center;
    background-size: cover;
    position: absolute;
    height: 85vh;
    animation-name: example4;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    z-index: 1;
    width: 200%;
}

@keyframes example4 {
    0% {left: -100%}
    100% {left: 0}
}


.state2 {
    background: aqua url("./secondRes.jpg") no-repeat center;
    background-size: cover;
    position: relative;
    height: 85vh;
    animation-name: example;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    z-index: 1;
}

@keyframes example {
    0% {opacity: 0; left: 100%}
    30% {opacity: 0.9}
    100% {opacity: 1; left: 0}
}

.transitionFirstAndSecond2 {
    z-index: 1;
    background: lightblue url("./firstAndSecondRes.jpg") no-repeat center;
    background-size: cover;
    height: 85vh;
    width: 200%;
    position: absolute;
    animation-name: example2;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
}

@keyframes example2 {
    0% {left: 0}
    100% {left: -100%}
}

.fog {
     z-index: 1000;
     background: url("./fog-png-transparent-1-transparent.png") no-repeat center;
